.menuContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  margin-top: 40px;
}

.menuContainer table {
  width: 100%;
  height: 100%;
  border: #000 solid 1px;
}
.menuContainer table th {
  width: 100%;
  height: 100%;
  padding: 10px;
  text-align: center;
  border-bottom: #000 solid 1px;

  background-color: white;
}
.menuContainer table tr {
  width: 100%;
  height: 100%;
}
.menuContainer table tr:nth-of-type(even) {
  background-color: #f2f2f2;
}
.menuContainer table tr:nth-of-type(odd) {
  background-color: white;
}

.menuContainer table tr td {
  width: 100%;
  height: 100%;

  padding-left: 20px;
  border-bottom: rgba(0, 0, 0, 0.2) solid 1px;
}

.menuContainer table tr td:first-child {
  text-align: left;
}

.menuContainer table tr td:last-child {
  text-align: center;
}

.menuContainer form {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.menuContainer form button {
  margin-top: 30px;
  padding: 10px;
  width: 20%;
  height: 70%;
  border: none;

  background-color: #fff;
  border: #000 solid 1px;
  color: #000;
  font-size: 1rem;
  cursor: pointer;
}
.menuContainer form button:hover {
  box-shadow: 0px 0px 2px #000;
}

.menuContainer table button {
  margin-top: 0;
  padding: 0;
  width: 100%;
  height: 50%;
  border: none;

  background-color: rgba(0, 0, 0, 0);
  color: #000;
  font-size: 1rem;
  cursor: pointer;
}

.menuContainer table .add:hover {
  box-shadow: rgba(0, 255, 0, 0.5) 0px 0px 10px;
}
.menuContainer table .sub:hover {
  box-shadow: rgba(255, 0, 0, 0.5) 0px 0px 10px;
}

@media screen and (max-width: 600px) {
  .menuContainer table th {
    font-size: 20px;
  }

  .menuContainer table td {
    font-size: 20px;
  }
  .menuContainer table th:nth-child(2) {
    display: none;
  }
    .menuContainer table th:nth-child(3) {
        display: none;
    }
  .menuContainer table td:nth-child(2) {
    display: none;
  }
  .menuContainer table td:nth-child(3) {
    display: none;
  }
  .menuContainer form button {
    width: 100%;
    height: 50%;
  }
  .menuContainer table button {
    width: 100%;
    height: 50%;
  }
}
